'use client';

import { useState, useEffect, FormEvent } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { useAuth } from '@/hooks/useAuth';
import { User, Mail, Phone, MapPin, Lock, Camera, ArrowLeft, Save, Loader2 } from 'lucide-react';
import Image from 'next/image';
import Link from 'next/link';

import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Separator } from "@/components/ui/separator";

export default function ProfilePage() {
    const { user, mutate } = useAuth({ middleware: 'auth' }); 
    const [isSaving, setIsSaving] = useState(false);

    const [infoData, setInfoData] = useState({
        name: '',
        email: '',
        nomor_telepon: '',
        alamat: '',
        foto: null as File | null,
    });
    const [previewFoto, setPreviewFoto] = useState<string | null>(null);

    const [passData, setPassData] = useState({
        current_password: '',
        password: '',
        password_confirmation: '',
    });

    useEffect(() => {
        if (user) {
            setInfoData({
                name: user.name,
                email: user.email,
                nomor_telepon: user.nomor_telepon || '',
                alamat: user.alamat || '',
                foto: null
            });
            setPreviewFoto(user.foto_url || null);
        }
    }, [user]);

    const handleUpdateInfo = async (e: FormEvent) => {
        e.preventDefault();
        setIsSaving(true);

        try {
            const formData = new FormData();
            formData.append('_method', 'PUT'); 
            formData.append('name', infoData.name);
            formData.append('email', infoData.email);
            formData.append('nomor_telepon', infoData.nomor_telepon);
            formData.append('alamat', infoData.alamat);
            
            if (infoData.foto) {
                formData.append('foto', infoData.foto);
            }

            await axios.post(`/api/users/${user?.id}?_method=PUT`, formData, {
                headers: { 'Content-Type': 'multipart/form-data' }
            });

            toast.success("Profil berhasil diperbarui!");
            mutate(); 

        } catch (error: any) {
            console.error(error);
            toast.error(error.response?.data?.message || "Gagal update profil.");
        } finally {
            setIsSaving(false);
        }
    };

    // --- HANDLER UPDATE PASSWORD ---
    const handleUpdatePassword = async (e: FormEvent) => {
        e.preventDefault();
        
        if (passData.password !== passData.password_confirmation) {
            return toast.error("Konfirmasi password tidak cocok.");
        }

        setIsSaving(true);
        try {
            await axios.put('/api/user/password', passData); 
            toast.success("Password berhasil diubah!");
            setPassData({ current_password: '', password: '', password_confirmation: '' });

        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal ubah password.");
        } finally {
            setIsSaving(false);
        }
    };

    const getDashboardLink = () => {
        if (user?.role?.name_role === 'Admin') return '/admin/dashboard';
        if (user?.role?.name_role === 'Karyawan') return '/karyawan/pos-kasir';
        return '/pelanggan/home';
    };

    if (!user) return null;

    return (
        <div className="min-h-screen bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
            <div className="max-w-4xl mx-auto space-y-6">
                
                {/* Header Nav */}
                <div className="flex items-center gap-4">
                    <Link href={getDashboardLink()}>
                        <Button variant="outline" size="icon" className="rounded-full">
                            <ArrowLeft className="h-4 w-4" />
                        </Button>
                    </Link>
                    <h1 className="text-2xl font-bold text-gray-900">Pengaturan Akun</h1>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                    
                    {/* KARTU PROFIL (KIRI) */}
                    <Card className="md:col-span-1 h-fit">
                        <CardContent className="pt-6 flex flex-col items-center text-center">
                            <div className="relative mb-4 group">
                                <div className="w-32 h-32 rounded-full overflow-hidden border-4 border-white shadow-lg bg-gray-100 relative">
                                    {previewFoto ? (
                                        <Image 
                                            src={previewFoto} 
                                            alt={user.name} 
                                            fill 
                                            className="object-cover"
                                            unoptimized 
                                            onError={() => setPreviewFoto(null)}
                                        />
                                    ) : (
                                        <div className="w-full h-full flex items-center justify-center text-gray-400 bg-gray-200 text-4xl font-bold select-none">
                                            {user.name.charAt(0).toUpperCase()}
                                        </div>
                                    )}
                                </div>
                                {/* Overlay Edit Foto */}
                                <label 
                                    htmlFor="foto-upload" 
                                    className="absolute inset-0 bg-black/40 rounded-full flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity cursor-pointer"
                                >
                                    <Camera className="text-white w-8 h-8" />
                                </label>
                                <input 
                                    id="foto-upload" 
                                    type="file" 
                                    accept="image/*" 
                                    className="hidden" 
                                    onChange={(e) => {
                                        const file = e.target.files?.[0];
                                        if (file) {
                                            setInfoData({ ...infoData, foto: file });
                                            setPreviewFoto(URL.createObjectURL(file));
                                        }
                                    }}
                                />
                            </div>

                            <h2 className="text-xl font-bold text-gray-900">{user.name}</h2>
                            <p className="text-sm text-gray-500 mb-4">{user.email}</p>
                            
                            <span className="inline-flex items-center px-3 py-1 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                                {user.role?.name_role}
                            </span>
                        </CardContent>
                    </Card>

                    {/* FORM TABS (KANAN) */}
                    <div className="md:col-span-2">
                        <Tabs defaultValue="info" className="w-full">
                            <TabsList className="w-full justify-start mb-6 bg-white p-1 rounded-lg border">
                                <TabsTrigger value="info" className="flex-1">Informasi Profil</TabsTrigger>
                                <TabsTrigger value="password" className="flex-1">Ganti Password</TabsTrigger>
                            </TabsList>

                            {/* TAB 1: EDIT INFO */}
                            <TabsContent value="info">
                                <Card>
                                    <CardHeader>
                                        <CardTitle>Data Diri</CardTitle>
                                        <CardDescription>Perbarui informasi profil akun Anda.</CardDescription>
                                    </CardHeader>
                                    <Separator />
                                    <CardContent className="pt-6">
                                        <form onSubmit={handleUpdateInfo} className="space-y-4">
                                            <div className="grid gap-2">
                                                <Label htmlFor="name">Nama Lengkap</Label>
                                                <div className="relative">
                                                    <User className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                                                    <Input id="name" className="pl-9" value={infoData.name} onChange={e => setInfoData({...infoData, name: e.target.value})} required />
                                                </div>
                                            </div>

                                            <div className="grid gap-2">
                                                <Label htmlFor="email">Email</Label>
                                                <div className="relative">
                                                    <Mail className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                                                    <Input id="email" type="email" className="pl-9" value={infoData.email} onChange={e => setInfoData({...infoData, email: e.target.value})} required />
                                                </div>
                                            </div>

                                            <div className="grid gap-2">
                                                <Label htmlFor="phone">Nomor Telepon</Label>
                                                <div className="relative">
                                                    <Phone className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                                                    <Input id="phone" className="pl-9" value={infoData.nomor_telepon} onChange={e => setInfoData({...infoData, nomor_telepon: e.target.value})} />
                                                </div>
                                            </div>

                                            <div className="grid gap-2">
                                                <Label htmlFor="address">Alamat</Label>
                                                <div className="relative">
                                                    <MapPin className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                                                    <Input id="address" className="pl-9" value={infoData.alamat} onChange={e => setInfoData({...infoData, alamat: e.target.value})} />
                                                </div>
                                            </div>

                                            <div className="flex justify-end pt-4">
                                                <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                                                    {isSaving ? <Loader2 className="h-4 w-4 animate-spin mr-2"/> : <Save className="h-4 w-4 mr-2"/>}
                                                    Simpan Perubahan
                                                </Button>
                                            </div>
                                        </form>
                                    </CardContent>
                                </Card>
                            </TabsContent>

                            {/* TAB 2: GANTI PASSWORD */}
                            <TabsContent value="password">
                                <Card>
                                    <CardHeader>
                                        <CardTitle>Keamanan</CardTitle>
                                        <CardDescription>Pastikan password Anda kuat dan aman.</CardDescription>
                                    </CardHeader>
                                    <Separator />
                                    <CardContent className="pt-6">
                                        <form onSubmit={handleUpdatePassword} className="space-y-4">
                                            <div className="grid gap-2">
                                                <Label>Password Saat Ini</Label>
                                                <div className="relative">
                                                    <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                                                    <Input type="password" className="pl-9" value={passData.current_password} onChange={e => setPassData({...passData, current_password: e.target.value})} required />
                                                </div>
                                            </div>

                                            <div className="grid gap-2">
                                                <Label>Password Baru</Label>
                                                <div className="relative">
                                                    <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                                                    <Input type="password" className="pl-9" value={passData.password} onChange={e => setPassData({...passData, password: e.target.value})} required />
                                                </div>
                                            </div>

                                            <div className="grid gap-2">
                                                <Label>Konfirmasi Password Baru</Label>
                                                <div className="relative">
                                                    <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                                                    <Input type="password" className="pl-9" value={passData.password_confirmation} onChange={e => setPassData({...passData, password_confirmation: e.target.value})} required />
                                                </div>
                                            </div>

                                            <div className="flex justify-end pt-4">
                                                <Button type="submit" disabled={isSaving} className="bg-gray-900 hover:bg-black">
                                                    {isSaving ? <Loader2 className="h-4 w-4 animate-spin mr-2"/> : 'Update Password'}
                                                </Button>
                                            </div>
                                        </form>
                                    </CardContent>
                                </Card>
                            </TabsContent>

                        </Tabs>
                    </div>
                </div>
            </div>
        </div>
    );
}